.page {
  font-family: "Poppins", sans-serif;
  font-size: 24px;
  line-height: 100%;
  color: $white-text;
  background-color: $page-bg;

  scroll-behavior: smooth;

  &:has(&__menu:target) {
    overflow: hidden;
  }

  &__body {
    margin: 0;
    min-width: 320px;
    overflow-x: hidden;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transition: all 0.5s;
    opacity: 0;
    transform: translateX(-100%);

    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}
