.nav {
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 32px;

  }

  &__link {
    position: relative;

    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
    color: $dark-text;
    text-decoration: none;

    &::after {
      content: "";
      position: absolute;
      bottom: -8px;
      display: block;
      height: 1px;
      width: 100%;
      background-color: $dark-text;

      transition: transform 0.5s linear;
      transform: scale(0);
      transform-origin: left;
    }

    &:hover::after {
      transform: scale(1);
    }
  }
}
