.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: $menu-bg;
  font-weight: 600;
  text-transform: uppercase;
  overflow: auto;

  &__top {
    margin-bottom: 24px;
    position: sticky;
    top: 0;
    z-index: 1;

    @include onTablet {
      margin-bottom: 0;
    }
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;

    font-size: 16px;
    line-height: 27px;
    color: $dark-text;
    text-decoration: none;
    @include hover(color, $link-change-color);
  }

  &__book-a-test-ride {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid $dark-text;

    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    color: $dark-text;
    text-decoration: none;

    @include hover(color, $link-change-color);
  }
}
